import { useState , useCallback , useEffect} from 'react';
import React,{ Fragment } from 'react';
function TodoList() {
    const [inputValue,inputChange] = useState('')
    const [data, listchange] = useState([])

    const onSubmit = useCallback (() => {
        listchange([...data,inputValue])
    })
    /* const remove = useCallback ((index) => {
        data.splice(index,1)
        listchange(data)
    }) */
    /* const remove = useEffect((index) => {
        data.splice(index,1)
        listchange(data)
    }) */;
    return (
      <Fragment>
            <input onChange={e => inputChange(e.target.value)} />
            <button onClick={onSubmit}>Submit</button>
            <ul>
                {data.map((value,index) => {
                    return (
                        <div key={index}>
                            <button onClick={index => {data.splice(index,1),listchange(data)}}>Remove</button>
                            <li>{value}</li>
                        </div>
                    )
                })}
            </ul>
      </Fragment>
    )
  }
export default TodoList;
